
.text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  list-style: none;
  border-bottom: 0;
}

.text.hidden {
  border-bottom: 1px solid #fff;
}

.text li {
  display: inline-block;
  float: left;
  font-weight: 700;
  font-size: 2em;
  color: #fff;
  opacity: 1;
  transition: all 0.8s ease-in-out;
  max-width: 2em;
}
.text.hidden li.spaced {
  padding-left: 0;
}
.text li.spaced {
  padding-left: 0.5em;
}

.text.hidden li.ghost {
  opacity: 0;
  max-width: 0;
}

span {
  font-family: 'Open Sans', sans-serif;
  font-size: 15px;
}

.bigTitle {
  font-size: 40px;
  font-style: bold;
  
  margin-top: 100px;
  margin-bottom: 100px;
}

h1 {
  font-size: 32px;
  text-decoration: none;
  font-weight: normal;
}

h1:before{
  content: "";
  position: absolute;
  width: 100px;
  height: 40px;
  bottom: -100;
  border-bottom: 2px solid orangered;

  -webkit-animation: mymove 2s 1;
  animation: mymove 2s 1;
}

@-webkit-keyframes mymove {
  from {width: 0px;}
  to {width: 100px;}
}

@keyframes mymove {
  from {width: 0px;}
  to {width: 100px;}
}

.arrow {
  text-align: center;
  position: absolute;
  top: 80%;
  left: 0; 
  right: 0; 
  margin: auto;
  width: 100px; 
  padding: 20px;
  cursor: pointer;
}
.bounce {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}
