img {
    width: 100px;
    height: auto;
}
.item {
    vertical-align: top;
    display: inline-block;
    text-align: center;
    margin: 50px;
    width: 250px;
    margin-bottom: 250px;
}
.content {
    position: absolute;
    top: 100vh;
    width: 100%;
    background-color: white;
}
.bestExpositions {
    display: block;
    position: relative;
    width: 100%;
    margin-bottom: 100px;
}
.started {
    margin-top: 80px;
    font-size: 50px;
    font-family: 'Source Sans', sans-serif;
}
.horizontalScroll {
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    transition: all 0.2s;
    transform: scale(0.98);
    will-change: transform;
    user-select: none;
    cursor: pointer;
}
.horizontalScroll::-webkit-scrollbar {
    display: none;
}