header {
    position: fixed;
    max-height: 90px;
    width: 100%;
    top: 0;
    z-index: 1;
    background-color: rgba(12, 9, 4, 0.9);
}
.nav-up {
    background-color: rgba(12, 9, 4, 0);
}
.logo {
    padding-left: 50px;
    width: 80px;
    height: 80px;
    float: left;
    z-index: 2;
}

#profile {
    width: 30px;
    height: 30px;
}

header > a {
    color: white;
    text-decoration: none;
    font-weight: bold;
    font-size: 20px;
    padding: 15px;
    margin: 10px;
    line-height: 25px;
    border-radius: 4px;
    transition: all 0.2s;
    float: right;
}
header > a:hover {
    background-color: #ddd;
    color: black;

}

header input[type="text"] {
    -webkit-border-radius: 35px;
    -moz-border-radius: 35px;
    border-radius: 35px;
    display:block;
    background-color: rgba(255,255,255,0.1);
    padding: 10px;
    margin: 20px;
    margin-left: 50px;
    width: calc(80% - 420px);
    float: left;
}

header button {
    -webkit-border-radius: 35px;
    -moz-border-radius: 35px;
    border-radius: 35px;
    margin: 20px;
    padding: 10px;
    margin-left: -50px;
    cursor: pointer;
    float: left;
}

header form {
    display: inline;
}

@media only screen and (max-width: 600px) {
    header input[type="text"] {
        display: block;
        width: calc(80% - 50px);
    }
    header > a {
        display: block;
        width: 90%;
    }
    header {
        position: fixed;
        overflow: hidden;
        max-height: 90px;
        -webkit-transition: max-height 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
        -moz-transition: max-height 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
        -ms-transition: max-height 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
        -o-transition: max-height 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
        transition: max-height 0.5s cubic-bezier(0.77,0.2,0.05,1.0); 
    }
    .open {
        max-height: 400px;
        transition: max-height 0.5s cubic-bezier(0.77,0.2,0.05,1.0); 
    }
  }

  .getStarted {
	-moz-box-shadow: 0px 1px 0px 0px #1c1b18;
	-webkit-box-shadow: 0px 1px 0px 0px #1c1b18;
	box-shadow: 0px 1px 0px 0px #1c1b18;
	background:-webkit-gradient(linear, left top, left bottom, color-stop(0.05, #ff8400), color-stop(1, #b55e00));
	background:-moz-linear-gradient(top, #ff8400 5%, #b55e00 100%);
	background:-webkit-linear-gradient(top, #ff8400 5%, #b55e00 100%);
	background:-o-linear-gradient(top, #ff8400 5%, #b55e00 100%);
	background:-ms-linear-gradient(top, #ff8400 5%, #b55e00 100%);
	background:linear-gradient(to bottom, #ff8400 5%, #b55e00 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff8400', endColorstr='#b55e00',GradientType=0);
	background-color:#ff8400;
	-moz-border-radius:18px;
	-webkit-border-radius:18px;
	border-radius:18px;
	border:2px solid #333029;
	display:inline-block;
	cursor:pointer;
	color:#ffff;
	padding:12px 16px;
    text-shadow:0px 1px 0px #ff0000;
    
}
.getStarted:hover {
	background:-webkit-gradient(linear, left top, left bottom, color-stop(0.05, #b55e00), color-stop(1, #ffbb00));
	background:-moz-linear-gradient(top, #ffd7ac 5%, #ffbb00 100%);
	background:-webkit-linear-gradient(top, #ffd7ac 5%, #ffbb00 100%);
	background:-o-linear-gradient(top, #ffd7ac 5%, #ffbb00 100%);
	background:-ms-linear-gradient(top, #ffd7ac 5%, #ffbb00 100%);
	background:linear-gradient(to bottom, #ffd7ac 5%, #ffbb00 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffd7ac', endColorstr='#ffbb00',GradientType=0);
	background-color:#ffd7ac;
}

