
.App {
  font-family: 'Source Sans Pro';
  text-align: center;
}
.loader {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #fff;
}
.loader h1 {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 50%;
  transform: translateY(-50%);
}
.loading-container {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  margin: auto;
  width: 200px;
  height: 200px;
  border: 15px solid orangered;
  background-color: white;
  text-align: center;
  vertical-align: top;

  animation: swingBig 3s infinite ease-in-out;
  transform-origin: center top;
  -moz-animation: 3s ease 0s normal none infinite swingBig;
  -moz-transform-origin: center top;
  -webkit-animation:swingBig 3s infinite ease-in-out;
  -webkit-transform-origin:top;
}
.round {
  position: absolute;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgba(225, 225, 225, 0.6);
  fill: #242124;
  z-index: 1;
  
  transition: 0.2s ease-out;
}
.round:hover {
  background-color: #242124;
  fill: #f1f1f1;
}
.left {
  top: 50%;
  margin-top: -25px;
  left: 20px;
}
.right {
  top: 50%;
  margin-top: -25px;
  right: 20px;
}
.gallery-container {
  position: fixed;
}

.text-overlay {
  position: fixed;
  width: 80%;
  left: 50%;
  transform: translate(-50%, 0);
}
div > h2 {
  color: white;
  padding-top: 120px;
  font-size: 60px;
}
.text-overlay > h3 {
  color: white;
  font-size: 40px;
}
.blinking-cursor {
  font-weight: 200;
  font-size: 40px;
  color: white;
  -webkit-animation: 1s blink step-end infinite;
  -moz-animation: 1s blink step-end infinite;
  -ms-animation: 1s blink step-end infinite;
  -o-animation: 1s blink step-end infinite;
  animation: 1s blink step-end infinite;
}

@keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

@-moz-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

@-webkit-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

@-ms-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

@-o-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

@keyframes swingBig {
  0%{-webkit-transform:rotate(-40deg)}
  50%{-webkit-transform:rotate(40deg)}
  100%{-webkit-transform:rotate(-40deg)}
}

@-moz-keyframes swingBig{
  0%{-moz-transform:rotate(-40deg)}
  50%{-moz-transform:rotate(40deg)}
  100%{-moz-transform:rotate(-40deg)}
}
@-webkit-keyframes swingBig{
  0%{-webkit-transform:rotate(-40deg)}
  50%{-webkit-transform:rotate(40deg)}
  100%{-webkit-transform:rotate(-40deg)}
}